@import 'base/reset'; // import & partial file
@import 'base/variable';

// mixin sass 
@mixin flex($direction, $wrap: nowrap) {
  display: flex;
  flex-direction: $direction;
  flex-wrap: $wrap;
}

.app {
 margin: 0 auto;
 width: 100%; 
 background: $second-background; // variable
 font-family: $font-stack;
 @include flex(column); // call mixin
}

// Header Style
.header {
  background: url("../assets/beach-dawn-evening.jpg") fixed;
  background-size: cover;
  background-position: center center;
  width: 100%;
  height: 100vh;
  @include flex(column);
  align-items: center;
}

// nesting sass
.nav {
  width: 960px;
  margin: 0 auto;

  ul {
    @include flex(row, wrap);
  }

  li {
    list-style: none;
    padding: 20px;
  }

  li:nth-child(1) {
    margin-right: auto;
  }

  a {
    color: $third-font-color;
    text-decoration: none;
    text-transform: uppercase;
  }

  // menjadi .nav a:hover {}
  & a:hover {
    text-decoration: underline;
  }
}

.nav__logo {
  color: $third-font-color;
  letter-spacing: 10px;
}

.header__desc {
  color: $third-font-color;
  padding: 20px;
  margin-top: 240px;
  font-size: 40px;
  letter-spacing: 3px;
}
.header__sub-desc {
  color: $third-font-color;
  font-size: 20px;
  width: 960px;
  text-align: center;
  line-height: 28px;
}

// About Style
.about {
  width: 960px;
  margin: 150px auto;
  @include flex(column);
  align-items: center;

  &__title {
    color: $first-font-color;
    margin-bottom: 20px;
    font-size: 27px;
  }

  &__desc {
    line-height: 27px;
    text-align: center;
  }

}

// Service Style
.service {
  width: 100%;
  height: 400px;
  margin-bottom: 150px;
  background: $main-background;
  @include flex(column, wrap);
  justify-content: center;
  align-items: center;

  &__container {
    @include flex(row, wrap);
  }

  &__title {
    color: $first-font-color;
    margin-bottom: 40px;
    font-size: 27px;
  }

  &__item {
    @include flex(column, wrap);
    width: 320px;
    height: auto;
    align-items: center;
  }
}

.service__item .fas {
  font-size: 40px;
}

.service__item-title {
  color: $first-font-color;
  margin: 10px 0px;
  font-weight: bold;
}

.service__item-desc {
  text-align: center;
  color: $second-font-color;
  line-height: 22px;
  width: 82%;
}

// why choose us
.choose {
  width: 960px;
  margin: auto;

  &__title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 27px;
  }

  &__desc {
    text-align: center;
    line-height: 27px;
  }
}

// Footer
.footer {
  width: 100%;
  background: $main-background;
  height: 200px;
  margin-top: 150px;
  @include flex(column);
  justify-content: center;

  &__sosmed {
    @include flex(row, wrap);
    justify-content: center;

    li {
      list-style: none;
      padding: 20px 20px;
    }

    a {
      color: $first-font-color;
      text-decoration: none;
    }
  }

  &__copyright {
    text-align: center;
  }
}

// Responsive Design Layout
@media screen and (max-width: 980px) {
  .header__desc {
    text-align: center;
    margin-top: 160px;
  }
  .header__sub-desc {
    width: 90%;
  }
  .nav {
    width: 100%;

    li {
      padding: 20px;
    }
    li:nth-child(1) {
      margin-right: 0px;
    }
    a {
      font-size: 13px;
    }
  }

  .about {
    width: 90%;
  }

  .service {
    @include flex(column);
    width: 100%;
    height: auto;

    &__title {
      margin-top: 50px;
    }

    &__item {
      margin: 20px auto;
    }
  }

  .choose {
    width: 90%;
  }
}